* {
    margin: 0;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
@font-face {
    font-family: 'Satisfy';
    src: 
            local('Satisfy'), 
            url(https://fonts.google.com/?category=Handwriting&selection.family=Satisfy.woff) format('woff');
}
#app {
    font-family: 'Satisfy';
}
#root {
    width: 500px;
    height: 500px;
}
#app {
    position: relative;
    width: 100%;
    border: 2px solid red;
}
#yearDiv {
    width: 100px;
    height: 300px;
    border: 2px solid black;
    overflow-y: scroll;
}
#yearDiv::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	border-radius: 10px;
	background-color: #F5F5F5;
}
#yearDiv::-webkit-scrollbar
{
	width: 12px;
	background-color: #F5F5F5;
}

#yearDiv::-webkit-scrollbar-thumb
{
	border-radius: 10px;
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,.3);
	background-color: #D62929;
}
.yearVal {
    height: 25px;
    padding: 5px;
}


